.menu-nav{
    display: grid;
    grid-template-columns: 0% 2fr 1fr 0.3fr 0%;
    font-size: larger;
    width: 100%;
    height: 80px;
    /* position: fixed; */
}
.logo{
    display: grid;
    grid-column:2/3;
    padding-top: 16px;
    /* padding-left: 70px; */
   
}
.logo h2{
    transition-duration: 1s;
}

.menu-link{
    grid:3/4;
    justify-content: flex-end;
}
.theme{
    grid:4/5;
    display: flex;
   margin-top: 20px;
   margin-right: 0px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition-duration: 1s;
}
.menu-nav .logo img{
    width: 40px;

}
.checkbox{
    display: none;
}

.menu-link ul{
    list-style: none;
    height: 70px;
    display:flex;
    align-items: center;

}
.menu-link ul >li{
    padding-right: 60px;
    color:black;
    display: inline;
    font-weight: 500;
    transition-duration: 1s;
}

.socialIcon .col-1{
    margin-right: 20px;
}
ul:hover li:not(:hover) {
    color: white;
    opacity: 0.5;
    filter: blur(1px);
  }
@media  screen and (max-width:750px) {
    .menu-nav .logo{
        padding-left: 30px;
    }
    .menu-nav .logo img{
        width: 30px;
    
    }
    .menu-nav{
        margin-bottom: 20px;
    }
}


@media  screen and (max-width:700px) {
    .menu-nav {
        height: 40px;
      
        grid-template-columns: 0% 2fr 2fr  0%;
        /* grid-template-columns: 2rem 2fr 2fr  2rem; */
      }
    
    .menu-nav .logo{
        height: 30px;
        margin-top: 10px;
        margin-left: -20px;
        margin-right: 25px;
        padding-left: 10px;
        padding-top: 5px;
        align-items:center ;
    }
    .menu-link ul >li{
      display: none;
    }
    .menu-nav .theme{
        height: 30px;
        width: 30px;
        margin-top: 12.5px;
        margin-right:50px;
        margin-left:-27.5px;
    }
}
@media  screen and (max-width:600px) {

    .menu-nav .logo img{
        width: 40px;
    
    }

}
@media screen and (max-width:400px) {
    .menu-nav {
        height: 40px;
      
        grid-template-columns: 0% 2fr 2fr  0%;
      }
      .menu-nav .logo{
        margin-bottom: 5px;
        margin-left: 3.5px;
      }
    .menu-nav .theme{
        /* margin:15px 0px 5px 3px; */
        margin-top: 12.5px;
        margin-right:30px;
        margin-left:-30px;
        
    }
}
@media  screen and (max-width:300px) {
    .menu-nav{
        grid-template-columns: 0% 3fr 2fr 0%;
    }
    .menu-nav .logo{
        margin:10px -5px 0px -10px;
    }
    .menu-nav .theme{
        height: 30px;
        width:30px;
       margin:15px -20px 0px 40px;
    }
    
}